import ThemeToggle from "./ThemeToggle";
import { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import axios from "axios";

const ReviewLogin = () => {

  const navigate = useNavigate();
  const [identifier, setIdentifier] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState("");

  const handleLogin = async (e) => {
    e.preventDefault();
    try {
      const response = await axios.post(`${process.env.REACT_APP_BACKEND_URL}/api/users/login`, {
        identifier,
        password,
      });

      const { token, user } = response.data;

      
     
      localStorage.setItem("token", token);
      localStorage.setItem("user", JSON.stringify(user));
      localStorage.setItem("userToken",token)
      localStorage.setItem("userId",user.id)

      navigate(-1);
    } catch (err) {
      setError("Invalid email or password");
      console.error(err);
    }
  };

  return (
    <>
      {/* ================================== Account Page Start =========================== */}
      <section className="account d-flex">
        <div className="account__left d-md-flex d-none flx-align section-bg position-relative z-index-1 overflow-hidden">
          <div className="account-thumb">
            <img src="assets/images/services/Login.png" alt="" />
            <div className="statistics animation bg-main text-center">
              {/* <h5 className="statistics__amount text-white">10k+</h5> */}
              <span className="statistics__text text-white font-14">
                Verified <br />
                Healthcare <br />
                Centre{" "}
              </span>
            </div>
          </div>
        </div>

        <div className="account__right padding-y-120 flx-align">
          <div className="account-content ">
            <Link to="/" className="logo mb-64">
              <img
                src="assets/images/logo/BHARATLOGO-01.png"
                alt=""
                className="white-version"
              />

              <img
                src="assets/images/logo/BHARATLOGO-01.png"
                alt=""
                className="dark-version"
              />
            </Link>

            <h4 className="account-content__title mb-35 text-capitalize">
              Welcome Back!
            </h4>
            {error && <div className="alert alert-danger">{error}</div>}
            <form onSubmit={handleLogin}>
              <div className="row gy-4">
                <div className="col-12">
                  {/* <label
                    htmlFor=""
                    className="form-label mb-2 font-18 font-heading fw-600"
                  >
                    Login As
                  </label> */}
                  {/* <div className="position-relative">
                    <select
                      id="loginAs"
                      className="common-input common-input--bg w-100"
                      defaultValue=""
                      value={role}
                      onChange={(e) => setRole(e.target.value)}
                    >
                      <option value="" disabled>
                        Select Role
                      </option>
                      <option value="admin">Admin</option>
                      <option value="centre">Centre</option>
                      <option value="user">User</option>
                    </select>
                  </div> */}
                </div>
                <div className="col-12">
                  <label
                    htmlFor="email"
                    className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                  >
                    Email or Phone
                  </label>

                  <div className="position-relative">
                    <input
                      type="text"
                      className="common-input common-input--bg common-input--withIcon"
                      id="email"
                      placeholder="Enter email or phoner"
                      value={identifier}
                      onChange={(e) => setIdentifier(e.target.value)}
                    />

                    <span className="input-icon">
                      <img src="assets/images/icons/envelope-icon.svg" alt="" />
                    </span>
                  </div>
                </div>

                <div className="col-12">
                  <label
                    htmlFor="your-password" 
                    className="form-label mb-2 font-18 font-heading fw-600" style={{fontFamily:"inter"}}
                  >
                    Password
                  </label>

                  <div className="position-relative">
                    <input
                      type="password"
                      className="common-input common-input--bg common-input--withIcon"
                      id="your-password"
                      placeholder="Enter Your Password"
                      value={password}
                      onChange={(e) => setPassword(e.target.value)}
                    />
                    <span
                      className="input-icon toggle-password cursor-pointer"
                      id="#your-password"
                    >
                      <img src="assets/images/icons/lock-icon.svg" alt="" />
                    </span>
                  </div>
                </div>

                <div className="col-12">
                  <div className="flx-between gap-1">
                    <Link
                      to="/forgot-password"
                      className="forgot-password text-decoration-underline text-main text-poppins font-14"
                    >
                      Forgot password?
                    </Link>
                  </div>
                </div>

                <div className="col-12">
                  <button
                    type="submit"
                    className="btn btn-main btn-lg w-100 pill"
                  >
                    {" "}
                    Sign In
                  </button>
                </div>

                <div className="col-sm-12 mb-0">
                  <div className="have-account">
                    <p className="text font-14">
                      Not Registered?{" "}
                      <Link
                        className="link text-main text-decoration-underline fw-500"
                        to="/user-register"
                      >
                        Register
                      </Link>
                    </p>
                  </div>
                </div>
              </div>
            </form>
          </div>
        </div>
      </section>
      {/* ================================== Account Page End =========================== */}
    </>
  );
};

export default ReviewLogin;
